import React from "react"
import styled from "@emotion/styled"
import { isBrowser } from "react-device-detect"
import { Link } from "gatsby"
import nav_up from "../images/nav_up.svg"
import nav_close from "../images/nav_close.svg"
import nav_down from "../images/nav_down.svg"
import color from "../components/color"

//Default footer
const TFooter = styled.footer`
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 5px 10px;
  z-index: -1;
  background-color: ${color.background_base};
  text-align: right;
`

export const DefaultFooter = ({ siteOwner }) => (
  <TFooter>
     {new Date().getFullYear()},
    {` `}
    <a href="/contact">{siteOwner}</a>
  </TFooter>
)

//Pagination footer
const PagingFooter = styled.div`

  width: 100%;
  height: 44px;
  z-index: 5;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  background-color: ${color.background_base};
  border-top: 1px solid ${color.border};
`

const Paging = styled.ul`
  margin: 0 auto;
  padding: 0;
  li {
    display: inline-block;
    padding: 5px 10px;
  }
`

const FooterLink = styled(Link)`
  display: inline-block;
  vertical-align: middle;
  color: ${color.text_white};
  border: 1px solid ${color.link};
  width: 30px;
  height: 30px;
  box-shadow: 1px 2px 2px 0 rgba(0,0,0,0.26);
  transition: transform 0.2s ease-out;
  line-height: 30px;
  
  background: rgb(74,122,186);
  background: linear-gradient(30deg, rgba(74,122,186,1) 0%, rgba(52,94,142,1) 50%, rgba(81,125,188,1) 50%);
  
  &:hover{
    transform: translateY( -3px) scale(1.03);
    transition: transform 0.3s ease-out;
  }
`

const ActiveLink = styled(FooterLink)`
  font-weight: bold;
  font-size: large;
  border: 1px solid rgba(235,163,59,1);
  color: ${color.text_white};
  background: rgb(206,126,18);
  background: linear-gradient(30deg, rgba(206,126,18,1) 0%, rgba(206,126,18,1) 50%, rgba(235,163,59,1) 50%, rgba(235,163,59,1) 100%);
`

export const PaginationFooter = ({currentPage, pageType, pageCount}) => (
  <PagingFooter>
    <Paging>
      {currentPage!==1 && (
        <li key={'prevpage'}>
          <FooterLink to={currentPage - 1 === 1 ? "/"+pageType+"/" : "/"+pageType+"/" + (currentPage - 1).toString()} rel="prev">
            &lt;
          </FooterLink>
        </li>
      )}

      {Array.from({ length: pageCount }, (_, i) => (
          <React.Fragment key={'frag_'+i}>
          {(
            (isBrowser && ((Math.abs(currentPage - (i+1)) <= 4) || (currentPage<5 && i<9) || (pageCount - currentPage < 5 && pageCount-(i+1)<9))) ||
            (!isBrowser && ((Math.abs(currentPage - (i+1)) <= 2) || (currentPage<3 && i<5) || (pageCount-currentPage<3 && pageCount-(i+1)<5)))
          ) && (
          <li key={'page_'+i}>
            { currentPage===i+1 && (
              <ActiveLink to={`/${pageType}/${i === 0 ? "" : i + 1}`}>
                {i + 1}
              </ActiveLink>
            )}
            { currentPage!==i+1 && (
              <FooterLink to={`/${pageType}/${i === 0 ? "" : i + 1}`}>
                {i + 1}
              </FooterLink>
            )}
          </li>
          )}
        </React.Fragment>
      ))}
      {currentPage!==pageCount && (
        <li key={'nextpage'}>
          <FooterLink to={"/"+pageType+"/" + (currentPage + 1).toString()} rel="next">
            &gt;
          </FooterLink>
        </li>
      )}
    </Paging>
  </PagingFooter>
)

const Navigator = styled.div`
  position: fixed;
  text-align: center;
  width: 100%;
  height: 38px;
  bottom: 0;
  padding-top: 6px;
  background-color: ${color.background_base};
  border-top: 1px solid ${color.border};
  z-index: 3;
    
  @media only screen and (min-width: 768px) {
    width: auto;
    height: auto;
    top: 90px;
    right: 30px;
    bottom: initial;
    display: inline-block;
    vertical-align: top;
    margin-left: 15px;
    background-color: initial;
    border-top: 0;
  }
`

const Nav = styled(Link)`
  width: 30px;
  height: 30px;
  margin: 0;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  display: inline-block;
  box-shadow: 1px 4px 5px 0 ${color.shadow};
  border: 1px ${color.icon_border} solid;
  position: relative;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0;
    box-shadow: 2px 7px 10px ${color.shadow};
    transition: opacity 0.2s ease-out;
  }

  &:hover{
    transform: translateY( -2px) scale(1.01);
    transition: transform 0.3s ease-out;
    &:after {
      opacity: 1;
      transition: opacity 0.2s ease-out;
    }
  }
  
  @media only screen and (min-width: 768px) {
    width: 50px;
    height: 50px;
  }
`

const NavUp = styled(Nav)`
  background-image: url(${nav_up});
  background-color: ${color.icon_navigator};
  
`

const NavClose = styled(Nav)`
  background-image: url(${nav_close});
  background-size: 70%;
  background-color: ${color.icon_close};
`

const NavDown = styled(Nav)`
  background-image: url(${nav_down});
  background-color: ${color.icon_navigator};
`

const NavSpacer = styled.div`
  display: inline-block;
  width: 10px;
  
  @media only screen and (min-width: 768px) {
    width: initial;
    display: block;
    height: 10px;
  }
`

export const NavigationFooter = ({currentPage}) => (
  <Navigator>
    <NavUp to={currentPage+ "/#top-anchor"}/>
    <NavSpacer/>
    <NavClose to={"/#"} onClick={(event) => {
      window.history.back()
      event.preventDefault()
    }}/>
    <NavSpacer/>
    <NavDown to={currentPage  + "/#bottom-anchor"}/>
  </Navigator>
)


