import React, { Fragment } from "react"
import { Highlight, Snippet } from "react-instantsearch-dom"
import { Link } from "gatsby"
import styled from "@emotion/styled"
import lineSeparator from "../../images/separator.svg"
import { LinkWithIcon } from "../style"
import { snakeCase } from "lodash"
import { ReactComponent as Tag } from "../../images/tag.svg"

const Categories = styled(LinkWithIcon)`
    height: 25px;
    margin-left: 10px;
`
const TagText = styled.span`
  vertical-align: middle;
`

const CardHeader = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 25px;
`

const CHTop = styled.div`
  width: 100%;
  height: 15px;
  float: left;
  //background-color: rgba(60,60,60,0.8);
  background: rgb(60,60,60);
    background: linear-gradient(30deg, rgba(40,40,40,0.8) 0%, rgba(81,85,84,0.8) 60%, rgba(81,85,84,0.8) 100%);
`

const CHDiagonal = styled.div`
  float: right;
  position: relative;
  width: 5px;
  height: 5px;
  
   &:before {
    content: "";
    display: block;

    /* position the triangle in the top right corner */
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;

    /* create the triangle */
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top-color: rgba(81,85,84,0.8);
    border-right-color:  rgba(81,85,84,0.8);
  }
`

const CHContent = styled.div`
  float: right;
  background: rgba(81,85,84,0.8);
  height: 10px;
`

const CHContentText = styled.div`
  margin-top: -15px;
  padding: 0 10px;
  color: #fefefe;
  &:before {
    content: "#";
    color: rgb(255,160,37);
    font-weight: bold;
    margin-right: 2px;
  }
`

const SearchTitle = styled.h3`
  margin: 0 0 5px 0;
  padding: 0
`

const PostHeader = styled.div`
  font-size: smaller;
  color: #d05641;
`

const ResultBox = styled.div`
  width: 450px;
  position: relative;
  text-align: left;
  mark {
    background-color: rgba(140,171,233,0.67);
  }
  
  &:hover{
    transform: translateY( -2px);
    transition: transform 0.3s ease-out;
  }
  
  
  @media only screen and (max-width: 768px) {
     width: calc(100vw - 20px);
  }
  
`

const PostContainer = styled.div`
  padding: 3px 10px;
  margin-top: 15px;
  color: #5c5c5c;
`
const ContentTagSeparator = styled.div`
  height: 5px;
  background-image: url(${lineSeparator});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-left: 1px solid #bebebe;
  border-right: 1px solid #bebebe;
  width: calc(100% - 2px);
  display: block;
  background-color: #fefefe;
`

const TagWrapper = styled.div`
  height: 30px;
  width: calc(100% - 33px);
  display: inline-block;
  vertical-align: top;
  border-left: 1px solid #bebebe;
  border-bottom: 1px solid #bebebe;
  background-color: #fefefe;
`

const DiagonalCut = styled.div`
  height: 30px;
  width: 33px;
  padding: 0;
  display: inline-block;
  position: relative;
  transform: skewX(-45deg);
  margin: 0 0 0 -18px;
  border-right: 1px solid #bebebe;
  background-color: #fefefe;
`

const ResultLink = styled(Link)`
  display: block;
  background-color: #fefefe;
  border: 1px solid #bebebe;
  border-bottom: 0;
`
export const PostHit = ({ hit, foldSearch }) => (
  <ResultBox>
    <ResultLink to={hit.path} onClick={foldSearch}>
      <CardHeader>
        <CHTop/>
        <CHContent>
             <CHContentText>{hit.postNumber}</CHContentText>
        </CHContent>
        <CHDiagonal/>
      </CardHeader>
      <PostContainer>
        <PostHeader>
          <Highlight attribute="date" hit={hit} tagName="mark" />
          {hit.date}
        </PostHeader>
        <SearchTitle>
          <Highlight attribute="title" hit={hit} tagName="mark" />
        </SearchTitle>
        <div>
        <Snippet attribute="excerpt" hit={hit} tagName="mark" />
        </div>
      </PostContainer>
    </ResultLink>
    <ContentTagSeparator />
    <TagWrapper>
      {hit.categories.map((tag, index) => (
        <Fragment key={tag}>
          <Categories key={"tag-" + tag } to={`/category/${snakeCase(tag)}/`}>
            <div><Tag/><TagText>{tag}</TagText>
            </div>
          </Categories>
        </Fragment>
      ))}
    </TagWrapper>
    <DiagonalCut/>
  </ResultBox>
)

export default PostHit