import { Link } from "gatsby"
import React from "react"
import styled from "@emotion/styled"
import Img from "gatsby-image"
import { Location } from "@reach/router"
import headerbg from "../images/headerbg.svg"
import Search from "../components/Search"
import {
  isMobile,
} from "react-device-detect"
import { ReactComponent as MenuSeparator } from "../images/separator_down.svg"
import { ReactComponent as ExpandBtnImg } from "../images/pointer.svg"
import back_btn from "../images/back.svg"
import menu_btn from "../images/burger.svg"
import search_btn from "../images/search.svg"
import color from "./color"

const searchIndices = [
  { name: `Posts`, title: `Blog Posts`, hitComp: `PostHit` },
]

const SearchBar = styled.div`
  @media only screen and (max-width: 768px) {
    position: fixed;
    top: 60px;
    right: calc(-90vw + 20px);
    z-index: 10;
    height: 45px;
    width: 100vw;
    text-align: center;
    padding: 0;
    transform: ${props => (props.active ? `translateX(calc(20px - 90vw))` : `none`)};
    transition: transform 0.3s ease-out;
    background-color: ${props => (props.active ? `#fefefe` : `initial`)};
    border: 1px solid ${color.border};
    border-width:  ${props => (props.active ? `1px` : `0`)};
    border-right: 0;
     
    div {
      grid-gap: 0;
    }
  
    form {
      text-align: center;
      input {
        margin-right: 0;
        width: calc(100vw - 50px);

      }
    }
  }
  @media only screen and (min-width: 768px) {
    position: fixed;
    top: 0;
    right: 0;
    margin: 10px 0;
    z-index: 10;
  }
  

`

const MobileExtendSearchButton = styled.div`
    width: 45px;
    height: 45px;
    position: absolute;
    z-index: 2;
    top: 2px;
    left: 5px;
    background-image: url(${search_btn});
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    
    @media only screen and (min-width: 768px) {
      display: none;
    }
`

const THeader = styled.header`
  width: 100%;
  background-color: ${color.background_base};
  z-index: 4;
  border-bottom: 1px solid ${color.border};
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;

  @media only screen and (min-width: 768px) {
    min-width: 1000px;
    border-top: 0;
  }
`

const LinkRow = styled.div`
  height: 30px;
  position: relative;
  background-color: ${color.background_base};
  text-align: center;
     
  @media only screen and (min-width: 768px) {
    height: 49px;
    width: 624px;
    overflow: hidden;
    display: inline-block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) scale(0.8);
  }
  
  @media only screen and (min-width: 1400px) {
      transform: translateX(-50%);
  }
`

const MainLink = styled.h1`
  display: block;
  position: relative;
  z-index: 10;
  background-color: ${color.background_base};
  text-align: center;
  padding: 0;

  @media only screen and (min-width: 768px) {
    display: inline-block;
    padding: 3px 10px;
    margin: 0 8px;
    font-size: 30px;
    border-bottom: 0;
    height: 40px;
  }
`

export const BrandIcon = styled(Img)`
  width: 15px;
  display: inline-block;
  vertical-align: middle;
  @media only screen and (min-width: 768px) { 
    width: 35px;
  }
`

const HeaderUl = styled.ul`
  margin: 0;
  padding: 0;
  position:relative;
  z-index: 3;
  @media only screen and (min-width: 768px) { 
    margin: 5px 0 1px;
  }
`

const HeaderLi = styled.li`
  position: absolute;
  width: 100vw;
  background-color: ${color.background_base};
  font-size: larger;
  padding: 0;
  display: block;
  text-align: center;
  transition: transform 0.3s ease-out;
  top: 0;
  left: 0;
  
  @media only screen and (min-width: 768px) {
    position: relative;
    background-image: url(${headerbg});
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: initial;
    display: inline-block;
    font-size: 16px;
    width: 140px;
    padding: 0 8px;
    transform: none;
  }
  
 a {
    font-weight: ${props => (props.active ? `600` : `400`)};
    color: ${props => (props.active ? color.link_active : color.link)};
  }
`


const HeaderHome = styled(HeaderLi)`
   z-index: 4;
   transform: ${props => (props.isExpanded ? `none` : props.active? `none` : `translateY(-100%)`)};
`

const HeaderBlog = styled(HeaderLi)`
  z-index: 3;
  transform: ${props => (props.isExpanded ? `translateY(100%)` : props.active? `none` : `translateY(-100%)`)};
`

const HeaderAbout = styled(HeaderLi)`
  z-index: 2;
  transform: ${props => (props.isExpanded ? `translateY(200%)` : props.active? `none` : `translateY(-100%)`)};
`

const HeaderSandbox = styled(HeaderLi)`
  z-index: 1;
  transform: ${props => (props.isExpanded ? `translateY(300%)` : props.active? `none`: `translateY(-100%)`)};
`

const HeaderContact = styled(HeaderLi)`
  z-index: 1;
  transform: ${props => (props.isExpanded ? `translateY(300%)` : props.active? `none`: `translateY(-100%)`)};
`

const HeaderButton = styled.li`
    @media only screen and (min-width: 768px) { 
      display: none;
    }
`



const HeaderLink = styled(Link)`
    text-transform: uppercase;
    line-height: 31px;
    letter-spacing: .7px;
    border: 0;
    display: block;
    width: 100%;

    @media only screen and (min-width: 768px) { 
      line-height: 42px;
      padding: 10px;
      display: inline;
      &:hover {
        color: ${color.link_hover};
      }
    }
`

const LinkShadow = styled.div`
  display: none;
  @media only screen and (min-width: 768px) {
    display: block;
    width:160px;
    height: 40px;
    position: absolute;
    top:6px;
    transform:  ${props =>
  (contains(props.path, ["/blog", "/post", "/category"]) ? `translateX(155px)` :
    (props.path.includes("/aboutme") ? `translateX(310px)` :
      (props.path.includes("/contact") ? `translateX(464px)` :
          `translateX(0.6px)`
      )))};
    transition: transform 0.2s ease-out;
  }
`

const SubLinkShadow = styled.div`
  background-color: ${color.link_active};
  height: 22px;
  padding: 0;
  margin: 0;
  position: absolute;
  width: 120px;
`

const SubLinkShadow1 = styled(SubLinkShadow)`
  top: 0;
  left: 0;
`

const SubLinkShadow2 = styled(SubLinkShadow)`
  bottom: 0;
  right: 0;
`

const HeaderTitle = styled.span`
  vertical-align: middle;
  font-size: 12px;
  line-height: 12px;
  margin: 0 3px;
  @media only screen and (min-width: 768px) {
    font-size: 25px;
    line-height: 35px;
  }
`

const PreHeaderTitle = styled(HeaderTitle)`
  margin-left: 10px;
  @media only screen and (min-width: 768px) {
    margin-left: 0;
  }
`

const Banner = styled(Link)`
  border-bottom: 1px solid ${color.border};
  display: inline-block;
  height: 25px;
  position: relative;
  padding: 0 35px;
  @media only screen and (min-width: 768px) {
    border-bottom: 0;
    height: initial;
    padding: initial;
  }
`

const Slope = styled.div`
  display: inline-block;
  height: 25px;
  width: 30px;
  position: absolute;
  top: -1px;
  background-color: ${color.background_base};
  z-index: -1;
`

const LeftSlope = styled(Slope)`
  transform: skewX(45deg);
  border-left: 1px solid ${color.border};
  left: -13px;
  @media only screen and (min-width: 768px) {
    display: none;
  }
`
const RightSlope = styled(Slope)`
  transform: skewX(-45deg);
  border-right: 1px solid ${color.border};
  right: -13px;
  @media only screen and (min-width: 768px) {
    display: none;
  }
`

const ExpandButton = styled(ExpandBtnImg)`
  height: 25px;  
  width: 25px;
  z-index: 10;
  display: inline-block;
  position: relative;
  top: 5px;
  left: 75px;
  transition: transform 0.3s ease-out;
  transform: ${props => (props.active ? `rotate(270deg)` : `rotate(90deg)`)};
  @media only screen and (min-width: 768px) {
    display: none;
  }
`

const Separator = styled(MenuSeparator)`
  width: 100vw;
  display: block;
  transition: transform 0.3s ease-out;
  background-color: ${color.background_base};
  position: absolute;
  top: 30px;
  left: 0;
  transform: ${props => (props.active ? `translateY(93px)` : `translateY(0)`)};
  
  @media only screen and (min-width: 768px) {
    display: none;
  }
`

const MobileButton = styled.div`
  display: inline-block;
  height: 35px;
  border-bottom: 1px solid ${color.border};
  width: calc(50vw - 112px);
  position: absolute;
  top: 0;
  background-color: ${color.background_base};
  @media only screen and (min-width: 768px) {
    display: none;
  }
  z-index: 11;
`

const LeftButton = styled(MobileButton)`
  left: 0;
  border-right: 1px solid  ${color.border};
  transform: skewX(-45deg);
`

const RightButton = styled(MobileButton)`
  right: 0;
  border-left: 1px solid  ${color.border};
  transform: skewX(45deg);
`

const BackButton = styled.div`
    width: 30px;
    height: 30px;
    position: fixed;
    z-index: 12;
    top: 12px;
    left: calc(10vw - 15px);
    background-image: url(${back_btn});
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    @media only screen and (min-width: 768px) {
      display: none;
    }
`

const MenuButton = styled.div`
    width: 30px;
    height: 30px;
    position: fixed;
    z-index: 12;
    top: 12px;
    right: calc(10vw - 15px);
    background-image: url(${menu_btn});
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    @media only screen and (min-width: 768px) { 
      display: none;
    }
`


function contains(target, pattern) {
  let value = 0
  pattern.forEach(function(word) {
    value = value + target.includes(word)
  })
  return (value === 1)
}

class HeaderElement extends React.Component {
  constructor(props) {
    super(props);
    this.state = {isExpanded: false, isSearchMode: false};
  }

  state = {
    isExpanded: false,
    isSearchMode: false,
  }

  preventClick = (e, currentPath) => {
    if (currentPath === 1) {
      e.preventDefault()
      e.stopPropagation()
      e.nativeEvent.stopImmediatePropagation()
    }
  }

  // function to handle the click
  expandClick = (e, currentPath, canExpand) => {
    if (canExpand || this.state.isExpanded ===true){
      this.setState(state => ({ isExpanded: !state.isExpanded }))
    }
    if (this.state.isExpanded === false || currentPath === 1) {
      this.preventClick(e, currentPath)
    }
  }

  expandSearch = () => {
    if (isMobile) {
      this.setState(state => ({ isSearchMode: true }))
    }
  }

  foldSearch = () => {
    if (isMobile) {
      this.setState(state => ({ isSearchMode: false }))
    }
  }

  render() {
    let data = this.props
    return (
      <>
        <THeader>
          <MainLink>
            <Banner to="/">
              <LeftSlope />
              <PreHeaderTitle>Hourai</PreHeaderTitle>
              <BrandIcon fluid={data.siteIcon.childImageSharp.fluid}/>
              <HeaderTitle>Etealune</HeaderTitle>
              <RightSlope/>
            </Banner>
          </MainLink>
          <LinkRow active={this.state.isExpanded}>
            <Location>
              {({ location }) => {
                return (
                  <>
                      <HeaderUl path={location.pathname}>
                        <HeaderHome isExpanded = {this.state.isExpanded ? 1 : 0} active={location.pathname === "/" ? 1 : 0} to="/" >
                          <HeaderLink to="/"
                                      className="lnk home"
                                      onClick={(e) => {
                                        this.expandClick(e, location.pathname === "/" ? 1 : 0, 1)
                                      }}>Home</HeaderLink>
                        </HeaderHome>
                        <HeaderBlog isExpanded = {this.state.isExpanded ? 1 : 0} active={(contains(location.pathname, ["/blog", "/post", "/category"])) ? 1 : 0}>
                          <HeaderLink className="lnk blog"
                                      to="/blog" onClick={(e) => {
                            this.expandClick(e, location.pathname === "/blog" ? 1 : 0, contains(location.pathname,["/post","/category","/blog/"])?0:1)
                          }}>Blog</HeaderLink>
                        </HeaderBlog>
                        <HeaderAbout isExpanded = {this.state.isExpanded ? 1 : 0} active={location.pathname.includes("/aboutme") ? 1 : 0} to="/aboutme">
                          <HeaderLink to="/aboutme"
                                      className="lnk about"
                                      onClick={(e) => {
                                        this.expandClick(e, location.pathname === "/aboutme" ? 1 : 0, 1)
                                      }}>About Me</HeaderLink>
                        </HeaderAbout>
                        {/*<HeaderSandbox isExpanded = {this.state.isExpanded ? 1 : 0} active={location.pathname.includes("/sandbox") ? 1 : 0} to="/sandbox">*/}
                        {/*  <HeaderLink to="/sandbox"*/}
                        {/*              className="lnk sandbox"*/}
                        {/*              onClick={(e) => {*/}
                        {/*                this.expandClick(e, location.pathname === "/sandbox" ? 1 : 0, 1)*/}
                        {/*              }}>Sandbox</HeaderLink>*/}
                        {/*</HeaderSandbox>*/}
                        <HeaderContact isExpanded = {this.state.isExpanded ? 1 : 0} active={location.pathname.includes("/contact") ? 1 : 0} to="/contact">
                          <HeaderLink to="/contact"
                                      className="lnk sandbox"
                                      onClick={(e) => {
                                        this.expandClick(e, location.pathname === "/contact" ? 1 : 0, 1)
                                      }}>Contact</HeaderLink>
                        </HeaderContact>
                        <HeaderButton>
                          <ExpandButton active={this.state.isExpanded ? 1 : 0} onClick={(e) => {
                            this.expandClick(e, 0,1)
                          }}/>
                        </HeaderButton>
                      </HeaderUl>
                      <BackButton onClick={(event) => {
                          window.history.back();
                          event.preventDefault();
                      }}/>
                      <MenuButton onClick={(e) => {
                        this.expandClick(e, 0,1)
                      }}/>
                      <LinkShadow path={location.pathname}>
                        <SubLinkShadow1/>
                        <SubLinkShadow2/>
                      </LinkShadow>
                      <Separator className={"separator"} active={this.state.isExpanded ? 1 : 0}/>
                    <LeftButton/>
                    <RightButton/>
                  </>
                )
              }}
            </Location>
          </LinkRow>
        </THeader>
        {(data.pageContext.layout === "blog" || data.pageContext.layout === "post")  && (
          <>
            <SearchBar active={this.state.isSearchMode?1:0}>
              <MobileExtendSearchButton onClick={this.expandSearch} isVisible={!this.state.isSearchMode}/>
              <Search collapse indices={searchIndices} foldSearch={this.foldSearch} isInPost={(data.pageContext.layout === "post"?1:0)} isActive={(isMobile && this.state.isSearchMode)?1:0} />
            </SearchBar>
          </>
        )}
      </>
    )
  }
}

export default HeaderElement
