import React, { useState, useEffect, createRef } from "react"
import styled from "@emotion/styled"
import {
  InstantSearch,
  connectStateResults,
  connectHits,
  PoweredBy,
} from "react-instantsearch-dom"
import algoliasearch from "algoliasearch/lite"
import PostHit from "./searchResult"
import Input from "./Input"
import color from "../color"

let queryLength = 0;
let searchInFocus = false;

const Root = styled.div`
  position: relative;
  display: grid;
  grid-gap: 10px;
`

const HitsWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: calc(100vh - 150px);
  @media only screen and (min-width: 768px) {
    height: calc(100vh -  51px);
    width: auto;
  }
`

//number of result
const Stats = connectStateResults(
  ({ searchResults: res }) =>
    res && `${res.nbHits} result${res.nbHits > 1 ? `s` : ``}`
)

const ResultHeader = styled.div`
  background-color: ${color.background_dark};
  color: white;
  display: block;
  font-size: larger;
  padding: 5px 10px;
  height: 40px;
`

//if clicking outside then remove?
const useClickOutside = (ref, handler, isActive, events) => {
    if (!events) events = [`mousedown`, `touchstart`]
    const detectClickOutside = event =>
      ref.current && !ref.current.contains(event.target) && handler()

    useEffect(() => {
      for (const event of events)
        if(searchInFocus || isActive){
          document.addEventListener(event, detectClickOutside)
        }

      return () => {
        for (const event of events)
          document.removeEventListener(event, detectClickOutside)
      }
    })

}


const CustomHits = connectHits(({ hits, foldSearch }) => (
  <ul>
    {hits.map(hit => (
      <li key={hit.objectID}>
        <PostHit hit={hit} foldSearch={foldSearch} />
      </li>
    ))}
  </ul>
));


const ResultWrapper = styled.div`
  background-color: rgba(59,59,59,0.7);
  height: calc(100% - 61px);
  overflow-y: auto;
  width: 100%;
  
  ul {
    list-style: none;
    padding: 5px 10px 0 10px;
    margin-top: 0;
  }
  li {
    margin: 0 0 10px;
  }
`

const CloseButton = styled.div`
  float: right;
  display: inline-block;
  color: #f1f1f1;
  cursor: pointer;
  font-size: x-large;
`

const ResultFooter = styled.div`
  background-color: ${color.background_dark};
  text-align: right;
  height: 30px;
  border-top: 1px solid ${color.background_dark};
`
const DiagonalFill = styled.div`
  width: 0; 
  height: 0; 
  display: inline-block;
  border-top: 15px solid transparent;
  border-left: 15px solid transparent;
  border-bottom:  15px solid ${color.background_base};
  border-right:  15px solid ${color.background_base};
  vertical-align: bottom;
`
const AlgoliaPowered = styled(PoweredBy)`
  display: inline-block;
  background-color: ${color.background_base};
  padding: 5px 20px;
  height: 20px;
  span {
    vertical-align: top;
  }
  svg {
    vertical-align: top;
    height: 20px;
  }
`

const CloseSearch = styled.div`
  position: absolute;
  top: 5px;
  right: 15px;
  font-size: 45px;
  line-height: 20px;
  color: ${color.border};
  @media only screen and (min-width: 768px) {
     display: none;
  }
`


const algoliaClient = algoliasearch(
  process.env.GATSBY_ALGOLIA_APP_ID,
  process.env.GATSBY_ALGOLIA_SEARCH_KEY
);

const searchClient = {
  search(requests) {
    if (queryLength <= 0 || !searchInFocus) {
      return;
    }
    return algoliaClient.search(requests);
  },
};

export default function Search({ indices, collapse, hitsAsGrid, foldSearch, isInPost, isActive }) {
  const ref= createRef()
  const [query, setQuery] = useState(``)
  const [focus, setFocus] = useState(false)
  queryLength = query.length;
  searchInFocus = focus;
  useClickOutside(ref, () =>  {setFocus(false); foldSearch()}, isActive)

  function fullClose() {
    setFocus(false);
    foldSearch();
  }

  return (
    <InstantSearch
      searchClient={searchClient}
      indexName="Blog"
      onSearchStateChange={({ query }) => setQuery(query)}
      root={{Root, props: { ref } }}
    >
      <Input onFocus={() => setFocus(true)} {...{ collapse, focus }} />
      <CloseSearch onClick={fullClose}></CloseSearch>
      {query.length>0 && focus && (
        <HitsWrapper isInPost={isInPost} isVisible={focus && isActive}>
          <ResultHeader>
            <Stats />
            <CloseButton onClick={fullClose}></CloseButton>
          </ResultHeader>
          <ResultWrapper><CustomHits key="searchBar" foldSearch={fullClose}/></ResultWrapper>
          <ResultFooter>
            <DiagonalFill/>
            <AlgoliaPowered/>
          </ResultFooter>
        </HitsWrapper>
      )}
    </InstantSearch>
  )

}