import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import Header from "../components/header"
import "./layout.css"
import styled from "@emotion/styled"
import {DefaultFooter, PaginationFooter, NavigationFooter} from "../components/footer"
import posed, { PoseGroup } from "react-pose"
import CategoryList from "../components/category_list"
const timeout = 350
const delay = 350

const MainBody = styled.div`
  padding: 0;
  overflow-x: hidden;
  @media only screen and (min-width: 768px) {
    overflow-x: initial;
  }
`

const Main = styled.div`
  text-align: center;
  margin: 60px auto 50px auto;
  
  @media only screen and (min-width: 768px) {
    margin: 50px auto;
  }
`

const Content = styled.div`
  display: inline-block;
  width: 100%;
  
  @media only screen and (min-width: 768px) {
    width: 1000px;
  }
  
  @media only screen and (min-width: 1400px) {
    width: 1280px;
  }
`

const Wrapper = styled.div`
  display: inline-block;
`

const RoutesContainer = posed(Wrapper)({
  enter: {
    delay: delay,
    opacity: 1,
    delayChildren: timeout,
    transition: { ease: 'easeOut', duration: timeout}
  },
  exit: {
    opacity: 0,
    delayChildren: delay + timeout,
    transition: { ease: 'easeOut', duration: timeout }
  },
})

const Layout = ({ children, location, pageContext }) => (
  <StaticQuery
    query={graphql`query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
        titleIcon: file(relativePath: { eq: "ribbonbadge.png" }) {
          childImageSharp {
            fluid(maxWidth: 30) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }`}

    render={data => (
      <MainBody>
        <Header className='h_header' siteTitle={data.site.siteMetadata.title} siteIcon = {data.titleIcon} pageContext={pageContext} />
        <Main>
            <Content>
              {(pageContext.layout === "blog") && (
                <CategoryList currentPage={pageContext}/>
              )}
              <PoseGroup>
                <RoutesContainer key={location.pathname}>{children}</RoutesContainer>
              </PoseGroup>
            </Content>
        </Main>

        {pageContext.layout !== "blog" && pageContext.layout !== "post" && (
          <DefaultFooter siteOwner={"Hourai Etealune"} />
        )}

        {pageContext.layout === "post" && (
          <NavigationFooter currentPage={location.pathname}/>
        )}

        {pageContext.layout === "blog" && (
          <>
            <PaginationFooter currentPage={pageContext.currentPage} pageType={pageContext.nPath} pageCount={pageContext.numPages}/>
          </>

        )}
      </MainBody>
    )}
  />
)

Layout.propTypes = {
    children: PropTypes.node.isRequired,
}

if (typeof window !== "undefined") {
  // eslint-disable-next-line global-require
  require("smooth-scroll")('a[href*="#"]', { speed: 150, easing: "easeOutQuart", updateURL: false, offset: 60 })
}

export default Layout