import styled from "@emotion/styled"
import { Link } from "gatsby"
import color from "./color"

export const LinkWithIcon = styled(Link)`
  text-decoration: none;
  color:  ${props => (props.active ? color.link_active : color.link)};
  font-weight: ${props => (props.active ? `bold` : `normal`)};;
  span {
    vertical-align: middle;
  }
  svg {
    margin-right: 3px;
    height: 17px;
    fill:  ${props => (props.active ? color.link_active : color.link)};
    vertical-align: middle;
  }

  div{
    display: inline-block;
    border-bottom: ${props => (props.active ? `1px solid `+color.link_active : `0`)};
  }    

  &:hover {
    color: ${color.link_hover};

    svg {
      fill: ${color.link_hover};
    }
    div {
      border-bottom: 1px dotted ${color.link_hover};
    }
  }
`

export const SidebarLinkWithIcon = styled(Link)`
  text-decoration: none;
  color:  ${color.tab_color};
  font-weight: ${props => (props.active ? `bold` : `normal`)};
  span {
    vertical-align: middle;
  }
  svg {
    margin-right: 5px;
    height: 20px;
    fill:  ${props => (props.active ? color.link_active : color.tab_color)};
    vertical-align: middle;
  }

  div{
    display: inline-block;
    height: 30px;
    border-bottom: ${props => (props.active ? `1px solid `+color.link_active : `0`)};
  }    

  &:hover {
    svg {
      fill: ${color.link_hover}
      padding-left: 5px;
    }
    div {
      &:hover {
      color: ${color.tab_color};
        border-right: 5px solid ${color.link_active}
        background-color: ${color.background_hover}
      }
    }
  }
`

export const Header = styled.div`
  border-bottom: 1px solid ${color.border};
  text-align: left;
`

export const HeaderNumber = styled.div`
  line-height: 100px;
  display: inline-block;
  border-right: 1px solid ${color.border}
  color: ${color.header_icon};
  text-align: center;  
  width: calc(25vw - 1px);
  font-size: 20vw;
  
  @media only screen and (min-width: 768px) {
    font-size: 90px;
    width: 119px;
  }
  
`

export const HeaderIcon = styled.div`
  display: inline-block;
  color: ${color.header_icon};
  vertical-align: middle;
  svg {
    padding: 10px;
    fill: ${color.header_icon};
    height: 60px;
    @media only screen and (min-width: 768px) {
      height: 90px;
    }
  }
`

export const HeaderTitle = styled.div`
 width: calc(75vw - 2px);
 text-align: center;
 min-height: 100px;
 border-left: 1px solid ${color.border};
 vertical-align: top;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
 
 h1 {
  font-weight: lighter;
  font-size: 11vw;
  line-height: 10vw;
  margin: 0;
 }

  @media only screen and (min-width: 768px) {
    width: 600px;
  h1 {
    font-size: 50px;
    line-height: 48px;
  }
} 
`

export const PostHeaderTitle = styled(HeaderTitle)`
  width: calc(75vw - 2px);
  min-height: 110px;
  border-left: 1px solid ${color.border};
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  vertical-align: middle;
  padding-top:10px;
  
  @media only screen and (min-width: 768px) {
    padding-left: 10px;
     width: 500px;
  }
  @media only screen and (min-width: 1400px) {
    width: 700px;
  }
  
`

export const ExpandableHeaderTitle = styled(HeaderTitle)`
  width: calc(100vw - 81px);
  vertical-align: middle;
  border-left: 1px solid ${color.border};
  min-height: 85px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;

  @media only screen and (min-width: 768px) {
     width: 600px;
     min-height: 115px;
  }
  @media only screen and (min-width: 1400px) {
    width: 800px;
    min-height: 115px;
  }
`

//Sidebar
export const RightSidebar = styled.div`
  display: inline-block;
  vertical-align: top;
  height: calc(100vh - 51px);
  border-right: 1px solid ${color.border};
  position: fixed;
  bottom: calc(-100vh + 95px);
  left: 0;
  transform: ${props => (props.active ? `translateY(-244px)` : `none`)};
  background-color: ${color.background_base};
  transition: transform 0.3s ease-out;
  border-top: 1px solid ${color.border};
  z-index: 3;
  width: 100vw;
  
  @media only screen and (min-width: 768px) {
   margin-bottom: -51px;
    position: sticky;
    top: 50px;
    left: 0;
    border-top: 0;
    transform: none;
    width: auto;
  }
  
`

