import React from "react"
import { graphql,  StaticQuery } from "gatsby"
import { snakeCase } from "lodash"
import styled from "@emotion/styled"
import { keyframes } from "@emotion/core"
import { ReactComponent as AllIcon } from "../images/all.svg"
import { ReactComponent as TagIcon } from "../images/tag.svg"
import { SidebarLinkWithIcon } from "./style"
import color from "./color"
import {
  MobileView,
  isMobile
} from "react-device-detect"
import { ReactComponent as DownIcon } from "../images/down.svg"

const opacity = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;


const CategoryPanel = styled.div`
  display: inline-block;
  vertical-align: top;
  height: calc(100vh - 51px);
  border-right: 1px solid ${color.border};
  background-color: ${color.background_base};
  position: fixed;
  bottom: calc(-100vh + 95px);
  left: 0;
  transform: ${props => (props.active ? `translateY(-244px)` : `none`)};
  transition: transform 0.3s ease-out;
  border-top: 1px solid ${color.border};
  z-index: 3;
  width: 100vw;
   
  @media only screen and (min-width: 768px) {
    margin-bottom: -51px;
    position: sticky;
    top: 50px;
    left: 0;
    transform: none;
    width: auto;
  }
  
`

const CategoryWrapper = styled.div`
  text-align: left;
  width: 244px;
  flex-wrap: wrap;
  display: inline-flex;
  //position: sticky;
  top: 0;
  left: 0;
  vertical-align: top;  
  background-color: ${color.background_base};
  margin-top: 5px;
  margin-right: 2px;
  animation: ${opacity} 2s ease-out 0s forwards;
`

const SideBarContent = styled.div`
  padding: 5px 0;
  width: 244px;
  display: inline-block;
  ul {
    list-style: none;
    padding-left: 0;
    text-align: left;
    margin: 2px auto;
  }
`
const PostCount = styled.span`
  float: right;
  line-height: 28px
`

const CategoryLink = styled.div`
  width: 212px;
  height: 25px;
  text-decoration: none;
  padding-right: 5px;
`

const CategoryName = styled.span`
  vertical-align: middle;
`

const SideBarTitle = styled.div`
  display: inline-block;
  margin-top: 5px;
`

const SideBarTitleText = styled.span`
  display: block;
  letter-spacing: 2px;
  padding-left: 10px;
`

const SubCategory = styled.div`
  margin-left: 25px;
`

const PullTab = styled.div`
  width: 125px;
  height: 25px;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 44px;
  z-index: 4;
  border-top: 1px solid ${color.border};
  transform: ${props => (props.active ? `translateY(-244px)` : `none`)};
  transition: transform 0.3s ease-out;
  background-color: ${color.background_base};
  @media only screen and (min-width: 768px) {
    display: none;
  }
`

const Slope = styled.div`
  display: inline-block;
  height: 25px;
  width: 30px;
  position: absolute;
  top: -1px;
  background-color: ${color.background_base};
  border-top: 1px solid ${color.border};
  z-index: -1;
`

const RightSlope = styled(Slope)`
  transform: skewX(45deg);
  border-right: 1px solid ${color.border};
  right: -13px;
`
const LeftSlope = styled(Slope)`
  transform: skewX(-45deg);
  border-left: 1px solid ${color.border};
  left: -13px;
`

const CloseTrigger = styled.div`
  width: 100vw;
  height: 100vh;
  position: absolute;
  top:0;
  left: 0;
  display: ${props => (props.active ? `block` : `none`)};
`

const DownIconImage = styled(DownIcon)`
  width: 17px;
  height: 17px;
  vertical-align: bottom;
  transform: ${props => (props.active ? `none` : `rotateX(180deg)`)};
  transition: transform 0.3s ease-out;
  fill: ${color.text};
`

const SubCategoryElement = (exData) => {
  const {currentCategory, clickFunction} = exData
  return (
    <StaticQuery query={
      graphql`
        query {
          allMarkdownRemark(limit: 2000) {
            totalCount
            group(field: frontmatter___categories) {
              fieldValue
              totalCount
            }
          }
        }
      `}  render={data => (
        <>
          <CategoryWrapper>
            <SideBarContent>
              <SideBarTitleText>HOURAI'S BLOG</SideBarTitleText>
              <SubCategory>
                <ul>
                  <li>
                    <SidebarLinkWithIcon to={`/blog/`}  active={(currentCategory === undefined)?1:0} onClick={clickFunction} >
                      <CategoryLink>
                        <AllIcon/>
                        <CategoryName>All Posts</CategoryName>
                        <PostCount>{data.allMarkdownRemark.totalCount}</PostCount>
                      </CategoryLink>
                    </SidebarLinkWithIcon>
                  </li>
                </ul>
              </SubCategory>
              <SideBarTitle>
                <SideBarTitleText>CATEGORIES</SideBarTitleText>
              </SideBarTitle>
              <SubCategory>
                <ul>
                  {data.allMarkdownRemark.group.map(category => (
                    <li key={category.fieldValue}>
                      <SidebarLinkWithIcon onClick={clickFunction} to={`/category/${snakeCase(category.fieldValue)}/`}  active={(currentCategory === category.fieldValue)?1:0}>
                        <CategoryLink>
                          <TagIcon/><CategoryName>{category.fieldValue}</CategoryName><PostCount>{category.totalCount}</PostCount>
                        </CategoryLink>
                      </SidebarLinkWithIcon>
                    </li>
                  ))}
                </ul>
              </SubCategory>
            </SideBarContent>
          </CategoryWrapper>
        </>
    )}
    />)
}

class CategoryList extends React.Component  {
  // ... constructor and toggleMenu from above
  state = {
    isExpanded: false
  };

  // function to handle the click
  expandClick = () => {
    if(isMobile) {
      this.setState(state => ({ isExpanded: !state.isExpanded }));
    }
  };

  foldClick = () => {
    if(isMobile) {
      this.setState(state => ({ isExpanded: false }));
    }
  };

  render() {
    return (
      <>
        <PullTab active={this.state.isExpanded?1:0} onClick={this.expandClick} > <LeftSlope />Tag: {this.props.currentPage.category===undefined?'All Posts':this.props.currentPage.category} <DownIconImage active={this.state.isExpanded ? 1:0} /> <RightSlope /> </PullTab>
        <CategoryPanel active={this.state.isExpanded?1:0} onClick={this.foldClick}>
          <SubCategoryElement currentCategory={this.props.currentPage.category} clickFunction = {this.foldClick} />
        </CategoryPanel>
        <MobileView>
          <CloseTrigger active={this.state.isExpanded?1:0} onClick={this.foldClick} />
        </MobileView>
      </>
    )
  }
}

export default CategoryList
